import React, { Component } from 'react';
import { BrowserRouter as Router, Route} from 'react-router-dom';
import Header from "./components/layout/Header";
import Todos from './components/Todos';
import AddTodo from "./components/AddTodo";
import About from "./components/pages/About"
import axios from 'axios';
// import uuid from 'uuid';
import './App.css';

/** Ref: https://www.youtube.com/watch?v=sBws8MSXN7A*/
class App extends Component {
  // constructor(props) {
  //   super(props);
  // }

  state = {
    todos: [

    ]
  }

  componentDidMount() {
    axios.get("https://jsonplaceholder.typicode.com/todos?_limit=10")
      .then(res => this.setState({todos: res.data}));
  }

  toggleComplete = (id) => {
    // Iterate through all todo elements
    this.setState({ todos: this.state.todos.map(todo => {
      if (todo.id === id) {
        todo.completed = !todo.completed;
      }
      return todo;
    })});
  }

  delTodo = (id) => {
    axios.delete("https://jsonplaceholder.typicode.com/todos/${id}")
      .then(res => 
        // Filter out any IDs that does not match the passed in ID
        // Then, set the state with the remaining IDs
        this.setState(
          {todos: [...this.state.todos.filter(todoElem => 
            todoElem.id != id
          )]}
        )
      );
  }

  // Add Todo
  addTodo = (title) => {
    axios.post(
      "https://jsonplaceholder.typicode.com/todos",
      {
        title: title,
        completed: false
      }
    )
      .then(res => this.setState({
          todos: [...this.state.todos, res.data]
        })
      );
  }

  render() {
    return (
      <Router>
        <div className="App">
          <div className="container">
            <Header />
            <Route exact path="/" render={props => (
              <React.Fragment>
                <AddTodo addTodo={this.addTodo} />
                <Todos todos={this.state.todos} markComplete={ this.toggleComplete }
                delTodo={this.delTodo}/>
              </React.Fragment>
            )} />

            <Route exact path="/about" component={About} />
          </div>
        </div>
      </Router>
    );
  }
}

export default App;
